import style from './InspiringQuote.module.css';
import {useEffect, useLayoutEffect, useState} from "react";
import random from "../../helpers/Random";

const quotes = [
    'Ze wszystkich ksiek na wiecie najlepsze historie mona znale na stronach paszportu.',
    'Wypenij swoje ycie przygodami, nie przedmiotami. Miej historie do opowiedzenia, nie rzeczy do pokazania.',
    'Podr sprawia, e stajesz si skromny. Dostrzegasz, jak mae miejsce zajmujesz w wiecie.  Gustav Flaubert',
    'Nie byam wszdzie, ale jest ono na mojej licie.  Susan Sontag',
    'wiat jest ksik i ci, ktrzy nie podruj, czytaj tylko jedn stron.  w. Augustyn',

];

const InspiringQuote = () => {
    const [quote, setQuote] = useState('');
    const [loading, setLoading] = useState(true);

    useEffect(() => setLoading(false), []);
    useLayoutEffect(() => setQuote(quotes[random(0, quotes.length)]), [loading]);

    return <p className={style.p}>{quote}</p>;
}
export default InspiringQuote;