export function firebaseErrorsHandler(response) {
    switch (response.data.error.message) {
        case "EMAIL_EXISTS":
            return "Ten adres email zosta ju wczeniej wykorzystany.";
        case "INVALID_EMAIL":
            return "Podany \"email\" nim nie jest. Poprawny przykadowy email. twjemail@witrynaSerweraNaKturymJestTwjEmail.pl";
        case "EMAIL_NOT_FOUND":
            return "Na ten email nie zostao zaoone adne konto.";
        case "INVALID_PASSWORD":
            return "Nie poprawne haso.";
        case "MISSING_PASSWORD":
            return "Nie podano hasa.";
        case "CREDENTIAL_TOO_OLD_LOGIN_AGAIN":
            return "Dane logowania s za stare, musisz si prze logowa."
        case "Unauthorized":
            return "Sesja wygasa nie mona autoryzowa zapytania do bazy danych. Naley si prze logowa.";
        default:
            return response.data.error.message;
    }
}